import React, { useEffect, useState } from 'react';
import $ from 'jquery';
import Popper from 'popper.js';
import FullCalendar from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid' // a plugin!
import localeZhTw from '@fullcalendar/core/locales/zh-tw'; // Import Traditional Chinese locale
import '../css/calendar/addition.css';
import '../css/calendar/style.css';
import moment from 'moment'; // Make sure to import moment if you're using it
import HolidaysData from '../json/hoildays.json';

const workingDays = [];
const startDate = new Date("2024-12-09"); // 9th Dec 2024
const endDate = new Date("2025-08-31"); // Extend to the end of January 2025

// Generate weekdays (Monday to Friday)
for (let date = startDate; date <= endDate; date.setDate(date.getDate() + 1)) {
    const day = date.getDay();
    if (day >= 1 && day <= 5) { // Monday to Friday
        workingDays.push({
            title: '工作日',
            start: date.toISOString().split('T')[0] + 'T08:15:00',
            end: date.toISOString().split('T')[0] + 'T16:45:00',
        });
    }
}

// Add alternating Saturdays starting from 21/12/2024
let workingSaturday = new Date(2024, 11, 22); // Start with 21st Dec 2024
while (workingSaturday <= endDate) {
    workingDays.push({
        title: '工作日',
        start: workingSaturday.toISOString().split('T')[0] + 'T09:00:00',
        end: workingSaturday.toISOString().split('T')[0] + 'T12:00:00',
        classNames: ['circular-day-number-work']
    });
    // Move to the next Saturday (add 14 days)
    workingSaturday.setDate(workingSaturday.getDate() + 14);
}

const MyCalendar = () => {

    const [holidays, setHolidays] = useState([]);

    useEffect(() => {
        const fetchHolidays = async () => {
            try {
                const holidayEvents = HolidaysData.vcalendar[0].vevent;

                // Create an array to hold all holidays, including Sundays
                const allHolidays = [...holidayEvents];

                // Add every Sunday of the year to the holidays
                const year = moment().year(); // Get the current year
                for (let month = 0; month < 12; month++) {
                    const date = moment(`${year}-${month + 1}-01`); // Start from the first day of each month
                    while (date.month() === month) {
                        if (date.day() === 0) { // Check if it's Sunday (0 is Sunday in moment.js)
                            allHolidays.push({
                                summary: '假日',
                                dtstart: [date.format('YYYYMMDD')],
                                dtend: [date.add(1, 'days').format('YYYYMMDD')],
                            });
                        }
                        date.add(1, 'days'); // Move to the next day
                    }
                }

                setHolidays(allHolidays);
            } catch (error) {
                console.error('Error fetching holidays:', error);
            }
        };

        fetchHolidays();
    }, [HolidaysData]);

    // Prepare events combining holidays and filtered working days
    const combinedEvents = holidays.map(holiday => ({
        title: holiday.summary, // Use the holiday summary as the title
        start: moment(holiday.dtstart[0], 'YYYYMMDD').toISOString(), // Convert the start date to ISO format
        end: moment(holiday.dtend[0], 'YYYYMMDD').toISOString(), // Convert the end date to ISO format
        allDay: true, // Mark as all-day event
    }));

    // Filter out working days that fall on holidays
    const filteredWorkingDays = workingDays.filter(event => {
        const eventDate = moment(event.start);
        return !holidays.some(holiday =>
            moment(holiday.dtstart[0], 'YYYYMMDD').isSame(eventDate, 'day')
        );
    });

    // Combine filtered working days with holiday events
    const finalEvents = combinedEvents.concat(filteredWorkingDays.map(event => ({
        title: event.title,
        start: event.start,
        end: event.end,
        allDay: event.allDay,
    })));

    const hasEvent = (date) => {
        return finalEvents.some(event => {
            const eventDate = moment(event.start).format('YYYY-MM-DD');
            return eventDate === date.format('YYYY-MM-DD');
        });
    };

    const handleDatesSet = (dateInfo) => {
        const cells = document.querySelectorAll('.fc-daygrid-day');

        cells.forEach(cell => {
            const cellDate = moment(cell.dataset.date);
            const dayNumber = cell.querySelector('.fc-daygrid-day-number');

            if (holidays.some(holiday => moment(holiday.dtstart[0], 'YYYYMMDD').isSame(cellDate, 'day'))) {
                dayNumber.classList.add('circular-day-number-holiday'); // Class for holidays
                dayNumber.classList.remove('circular-day-number-work'); // Class for holidays
            } else {
                if (hasEvent(cellDate)) {
                    if (dayNumber) {
                        dayNumber.classList.add('circular-day-number-work'); // Class for working days
                        dayNumber.classList.remove('circular-day-number-holiday'); // Class for working days
                    }
                }
            }
            
        });
    };

    const today = moment();
    const validRange = {
        start: moment("2024-12-09").format('YYYY-MM-DD'),
        /* end: today.clone().add(45, 'days').format('YYYY-MM-DD'), */
    };

    return (
        <FullCalendar
            plugins={[dayGridPlugin]} // Plugin to enable the day grid view
            initialView="dayGridMonth" // Initial view of the calendar (month view)
            locales={[localeZhTw]} // Array of locales to support, in this case, Traditional Chinese
            locale="zh-tw" // Set the locale for the calendar to Traditional Chinese
            timeZone="local" // Use the local timezone for displaying dates and times
            headerToolbar={{
                left: 'prev,next today', // Buttons on the left side for navigation
                center: 'title', // Centered title displaying the current month
                right: '' // Right side button to switch to the month view
            }}
            validRange={validRange} // Restrict the calendar view to the specified valid range
            events={finalEvents} // Array of events to display in the calendar
            nextDayThreshold="09:00:00" // Threshold to determine when to consider day transitions
            displayEventEnd={false} // Whether to display the end time of events on the calendar
            firstDay={0} // Set the first day of the week to Sunday (0 = Sunday)
            weekNumbers={false} // Disable week numbers in the calendar
            selectable={true} // Allow selection of dates on the calendar
            weekNumberCalculation="ISO" // Use ISO week number calculation
            navLinks={false} // Enable clickable links on dates to navigate to day/week views
            defaultTimedEventDuration='01:00:00' // Default duration for timed events in hours:minutes:seconds
            editable={true} // Allow events to be editable (draggable)
            slotLabelFormat='HH:mm' // Format for time slots in the calendar
            weekends={true} // Show weekends (Saturday and Sunday) in the calendar
            nowIndicator={true} // Show an indicator for the current time
            dayPopoverFormat='dddd DD/MM' // Format for the date shown in the day popover
            longPressDelay={0} // Delay for long-press events (in milliseconds)
            eventLongPressDelay={0} // Delay for long-press events on calendar events (in milliseconds)
            selectLongPressDelay={0} // Delay for long-press to select dates (in milliseconds) 
            dayCellContent={({ date }) => date.getDate()} // Custom content for day cells (just the day number)
            fixedWeekCount={false} // Disable fixed week count (allowing for varying number of weeks in a month)
            showNonCurrentDates={false} // Hide dates outside the current month (non-current dates)
            datesSet={handleDatesSet} // Callback fired when the dates in the calendar are set or changed
            dayCellDidMount={(info) => {
                const cellDate = moment(info.date);
                // Check if the cell date is a holiday
                if (holidays.some(holiday => moment(holiday.dtstart[0], 'YYYYMMDD').isSame(cellDate, 'day'))) {
                    const dayNumber = info.el.querySelector('.fc-daygrid-day-number');
                    if (dayNumber) {
                        dayNumber.classList.add('circular-day-number-holiday'); // Class for holidays
                    }
                } else if (hasEvent(cellDate)) {
                    const dayNumber = info.el.querySelector('.fc-daygrid-day-number');
                    if (dayNumber) {
                        dayNumber.classList.add('circular-day-number-work'); // Class for working days
                    }
                }
            }}
           /*  eventContent={(eventInfo) => {
                return (
                    <div className="event_item">
                        <div className="event_text">
                            {eventInfo.event.title} 
                        </div>
                    </div>
                );
            }} */
        />
    );
};

export default MyCalendar;